@charset "UTF-8";
body {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
  color: #2a2825;
  background: #f8fafc;
}

minSupport {
  display: inline-flex;
  font-weight: 600;
  font-size: 1em;
  line-height: 1;
  font-family: Calibre, "Work Sans", sans-serif;
}

minSupport::before {
  content: 'desk';
  color: #7754fb;
  font-weight: 300;
}

minSupport::after {
  content: 'Bear';
  color: #2a2825;
  margin: 0 0 0 0.05em;
}

.smooth {
  -webkit-transition: all 0.15s ease-in-out;
  -moz-transition: all 0.15s ease-in-out;
  -o-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
}

button {
  display: flex;
  vertical-align: middle;
  align-items: center;
  padding: 17px 22px;
  font-weight: 400;
  border: none;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  text-align: center;
  background: #7754fb;
  color: #fff;
  font-size: 0.9em;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
}

button:hover {
  cursor: pointer;
  box-shadow: inset 0 0 0 1px #82817d;
  background: #82817d;
  padding: 17px 18px 17px 22px;
}

button .icon {
  font-size: 1.2em;
  line-height: 1;
  margin-left: 10px;
}

button:hover .icon {
  margin-left: 14px;
}

#container {
  display: flex;
  vertical-align: middle;
  align-items: center;
  width: 100%;
  min-height: 100%;
  justify-content: center;
}

#install {
  display: block;
  align-self: center;
  min-width: 500px;
  max-width: 500px;
  margin-bottom: 34px;
  margin-top: -10px;
  padding: 40px;
  border: 1px solid #e2e2e2;
  box-shadow: 0 2px 0 0 rgba(0, 0, 0, 0.05);
  border-radius: 3px;
  background: #fff;
}

#install .header {
  margin-bottom: 25px;
}

#install .header minSupport {
  font-size: 2em;
}

#install .header .tag {
  display: block;
  color: #82817d;
  font-size: 1.1em;
  line-height: 1.45;
  margin-top: 10px;
}

#install .howto {
  display: block;
  align-self: center;
  width: 100%;
  line-height: 1.5;
  margin-bottom: 25px;
}

#install .howto ul {
  display: block;
  list-style: none;
  padding: 0;
  margin: 0;
}

#install .howto ul li {
  margin-bottom: 10px;
  padding: 17px 16px 16px 16px;
  border-radius: 3px;
  background: #fff7d4;
  color: rgba(0, 0, 0, 0.6);
  margin-top: 12px;
  font-size: 0.9em;
}

#install .button-bar {
  display: flex;
  justify-content: space-between;
  width: 100%;
  vertical-align: middle;
  align-items: center;
  color: #82817d;
}

#footer {
  text-align: center;
  color: #82817d;
  font-size: 0.9em;
  text-transform: uppercase;
  font-family: Calibre, "Work Sans", sans-serif;
}

#footer a {
  text-decoration: none;
  color: #7754fb;
}

#footer a:hover {
  color: #2a2825;
}

#footer dot::before {
  content: "•";
  margin: 0 10px;
  color: #ddd;
}
